import { Component, OnInit } from '@angular/core';
import { DirektoriPerjalananDinasService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router } from '@angular/router';
import { PegawaiDetailService } from '../../../service/pegawai-detail.service';
import { NzMessageService } from 'ng-zorro-antd';
import { Perjalanan_dinas } from 'src/app/sdk/hr/models';

@Component({
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.scss']
})
export class ListComponent implements OnInit {

    constructor(
        private direktoriPerjalananDinasService: DirektoriPerjalananDinasService,
        private spinner: NgxSpinnerService,
        private router: Router,
        private pegawaiDetailService: PegawaiDetailService,
        private nzMessageService: NzMessageService
      ) { }

      id_pegawai: string= ''
      dataPerjalanan: Perjalanan_dinas[]=[]
      id_perjalanan_dinas: string= ''
      ngOnInit() {
        this.pegawaiDetailService.getIdPegawai().subscribe(data => {
            this.id_pegawai = data
            console.log('id pegawai paja tu ' + this.id_pegawai)
        })
        this.getPerjalanan()
      }


      getPerjalanan() {
          this.direktoriPerjalananDinasService.getDirektoriPerjalananDinas(JSON.stringify({id_pegawai: this.id_pegawai, sortKey: this.sortKey, sortValue: this.sortValue  })).subscribe(
              data=>{
                  this.dataPerjalanan= data.data
              }
          )
      }

      sortKey: string | null = null;
      sortValue: string | null = null;
      sort(sort: { key: string; value: string }): void {
          this.sortKey = sort.key;
          this.sortValue = sort.value;
          this.getPerjalanan();
      }

      delete(id) {
        this.spinner.show()
        this.direktoriPerjalananDinasService
            .deleteDirektoriPerjalananDinasId(id)
            .subscribe(data => {
                this.getPerjalanan()
                this.spinner.hide()
            })

    }

    batalDelete() {
        this.nzMessageService.info('click cancel')
    }

}
