import { Component, OnInit } from '@angular/core';
import { DirektoriPerjalananDinasService, DirektoriPerjalananDinasAnggotaService, DirektoriPegawaiMasterService, DirektoriPerjalananDinasRonService, AbsensiPilotRonService } from 'src/app/sdk/hr/services';
import { NzNotificationService } from 'ng-zorro-antd';
import { NgxSpinnerService } from 'ngx-spinner';
import { PegawaiDetailService } from '../../../service/pegawai-detail.service';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Perjalanan_dinas, Perjalanan_dinas_anggota, Ron } from 'src/app/sdk/hr/models';
import { FormControl, Validators } from '@angular/forms';

@Component({
  selector: 'app-edit',
  templateUrl: './edit.component.html',
  styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

    constructor(

        private direktoriPerjalananDinasService: DirektoriPerjalananDinasService,
        private direktoriPerjalananDinasRonService: DirektoriPerjalananDinasRonService,
        private notification: NzNotificationService,
        private direktoriPegawaiMasterService: DirektoriPegawaiMasterService,
        private absensiPilotRonService: AbsensiPilotRonService,
        private spinner: NgxSpinnerService,
        private pegawaiDetailService: PegawaiDetailService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private router: Router,
        private activatedRoute: ActivatedRoute

    ) { }

    dataPerjalanan: Perjalanan_dinas = {}
    listRon: Ron[] = []

    id_perjalanan_dinas: string = ''

    id_pegawai: string = ''

    ngOnInit() {
        this.pegawaiDetailService.getIdPegawai().subscribe(data => {
            this.id_pegawai = data;
        })
        this.activatedRoute.paramMap.subscribe(data => {
            if (data.get('id_perjalanan_dinas')) {
                this.id_perjalanan_dinas = data.get('id_perjalanan_dinas')
                console.log(data.get('id_perjalanan_dinas'))

                this.form()
                this.getData()
                this.getRon()
                this.getPreeData()
            }

        })
    }

    getPreeData() {

        this.absensiPilotRonService.getAbsensiPilotRons().subscribe(
            data => {
                this.listRon = data.data;
            }
        )
    }

    id_ron_kode: string = '';
    tanggal: string = '';
    tanggal_selesai: string = '';
    tambah_ron() {
        if (this.id_ron_kode == '' || this.id_ron_kode == null) {
            this.notification.blank(
                'Notification Title',
                'This is the content of the notification. This is the content of the notification. This is the content of the notification.',
            )
            return false
        }

        this.direktoriPerjalananDinasRonService.postDirektoriPerjalananDinasRons({
            id_perjalanan_dinas: this.id_perjalanan_dinas,
            id_ron_kode: this.id_ron_kode,
        }).subscribe(res => {
            this.getRon()
        })
    }

    listPerjalananRon: any[] = []
    getRon() {
        let filter = {
            id_perjalanan_dinas: this.id_perjalanan_dinas,
        }
        this.direktoriPerjalananDinasRonService.getDirektoriPerjalananDinasRons(JSON.stringify(filter)).subscribe(
            res => {
                this.listPerjalananRon = res.data
            })
    }
    deleteRon(id) {
        this.spinner.show()
        this.direktoriPerjalananDinasRonService.deleteDirektoriPerjalananDinasRonsId(id).subscribe(data => {
            this.getRon()
            this.spinner.hide()
        })
    }

    getData() {
        this.spinner.show()
        this.direktoriPerjalananDinasService.getDirektoriPerjalananDinasId(this.id_perjalanan_dinas).subscribe(
            data=> {
                this.dataForm.setValue(data.data);
                this.spinner.hide()
            }
        )
    }

    dataForm: FormGroupTypeSafe<Perjalanan_dinas>
    form() {
        this.dataForm = this.fb.group<Perjalanan_dinas>({
            id_perjalanan_dinas: new FormControl(''),
            tujuan_dinas: new FormControl('', [Validators.required]),
            keperluan_dinas: new FormControl(''),
            tanggal_berangkat: new FormControl(''),
            tanggal_kembali: new FormControl(''),
            transportasi: new FormControl(''),
            id_pegawai: new FormControl(''),
            uang_transport: new FormControl(''),
            uang_penginapan: new FormControl(''),
            uang_saku: new FormControl(''),
            uang_dll: new FormControl(''),
            no_dokumen: new FormControl(''),
            created_at: new FormControl(''),
            updated_at: new FormControl(''),
            id_data_flow: new FormControl(''),
            id_data_flow_detail: new FormControl(''),
            posisi: new FormControl(''),
            nomor: new FormControl(''),
            validasi: new FormControl(''),
            status: new FormControl(''),
            id_user: new FormControl(''),
            id_dokumen: new FormControl(''),
        })
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }



        this.dataPerjalanan = this.dataForm.value
        this.direktoriPerjalananDinasService
            .putDirektoriPerjalananDinasId({id:this.id_perjalanan_dinas, body: this.dataPerjalanan}
            )
            .subscribe(
                data => {

                    this.goToBack()
                    this.spinner.hide()
                },
                err => {
                    this.pesan.pesanWarningForm('Gagal di simpan')
                    this.spinner.hide()
                },
                () => {
                    console.log('HTTP request completed.')
                },
            )
    }

    goToBack() {
        this.router.navigate(['/hr/direktori/pegawai-detail', this.id_pegawai, 'lainnya','perjalanan-dinas','list'])
    }

}
